
interface Profile {
  id: number;
  account: string;
  character: number;
}

// Ïù¥Í±∞ Î∞∞Ïó¥Î°ú Î∞îÍøîÏ§òÏïºÎêå ÌÉÄÏûÖ -> Î∞∞Ïó¥Î°úÎ∞îÍøîÏÑú Ï†ëÍ∑º 
export const contentsSort = (data: any[],type:string): any[] => {
  return [...data].sort((a, b) => {
    // profiles Í∞ùÏ≤¥ ÎÇ¥Î∂ÄÏùò Í∞íÏóê Ï†ëÍ∑º (ÌïµÏã¨!)
    const accA = a[type]?.account ?? '';
    const accB = b[type]?.account ?? '';

    const getWeight = (val: string) => {
      if (val === 'U') return 0;  // 1Îì±: U
      if (val === '') return 1;   // 2Îì±: Îπà Í∞í
      return 2;                   // 3Îì±: ÎÇòÎ®∏ÏßÄ
    };

    const weightA = getWeight(accA);
    const weightB = getWeight(accB);

    if (weightA !== weightB) {
      return weightA - weightB;
    }

    if (accA !== accB) {
      return accA.localeCompare(accB);
    }

    // characterÎèÑ profiles ÏïàÏóê ÏûàÏúºÎØÄÎ°ú Í≤ΩÎ°ú ÏàòÏ†ï
    return (a.profiles?.character ?? 0) - (b.profiles?.character ?? 0);
  });
};